.writings {
    width: 80%;
    font-family: "Questrial", sans-serif;
    color: #ffffff;
}

.writings > div {
    margin-bottom: 10vh;
}

.writings-details {
    display: flex;
    flex-direction: row;
    margin-bottom: 2em;
    align-items: flex-start;
}

.writings-details > b {
    font-size: 2em;
    margin-right: 1em;
}

.writings-details > div > div:first-child {
    font-size: 2em;
}

.writings-details > div > div:last-child {
    font-size: 1.5em;
    font-style: italic;
}

.writings-details a {
    cursor: pointer !important;
    text-decoration: none;
    color: #ffffff;
}

@media screen and (max-width: 959px) and (orientation: portrait) {
    .writings-details > b,
    .writings-details > div > div:first-child {
        font-size: 1.65em;
    }

    .writings-details > div > div:last-child {
        font-size: 1.23em;
    }
}

@media screen and (max-width: 767px) and (orientation: portrait) {
    .writings-details > b,
    .writings-details > div > div:first-child {
        font-size: 4.5vw;
    }

    .writings-details > div > div:last-child {
        font-size: 3.37vw;
    }
}