.press {
    width: 80%;
    font-family: "Questrial", sans-serif;
    color: #ffffff;
    overflow: scroll;
}

.press > div {
    margin-bottom: 10vh;
}

.press-items-holder {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.press-details {
    display: flex;
    flex-direction: column;
    margin: 0 2em;
    align-items: flex-start;
    overflow: hidden;
    flex: 1;
    width: auto;
}

.press-details > b {
    font-size: 2em;
    margin-right: 1em;
    margin-bottom: 1em;
}

.press-details > div {
    font-size: 2em;
    font-style: italic;
}

.press-details a {
    cursor: pointer !important;
    text-decoration: none;
    color: #ffffff;
}

.press-dates {
    display: flex;
    color: #ffffff;
    flex-direction: row;
    flex-wrap: wrap;
    width: 80%;
    justify-content: space-evenly;
    margin-bottom: 5vh;
}

.press-dates > div {
    font-family: "Questrial", sans-serif;
    font-weight: bold;
    font-size: 1.5em;
    margin: 0.5em 0;
    width: 11%;
    text-align: center;
}

@media screen and (min-width: 959px) {
    .press-details {
        min-width: 40em;
    }
}

@media screen and (max-width: 959px) {
    .press-details {
        min-width: 60vw;
    }
}

@media screen and (max-width: 959px) and (orientation: portrait) {

    .press-details > b,
    .press-details > div {
        font-size: 1.65em;
    }

    .press-dates > div {
        font-size: 1.23em;
    }
}

@media screen and (max-width: 767px) and (orientation: portrait) {

    .press-details > b,
    .press-details > div {
        font-size: 4.5vw;
    }

    .press-dates > div {
        font-size: 3.37vw;
    }
}


.press-film-strip {
    width: 1000vw;
    height: 5em;
    background-image: url("../assets/press/film_strip.png");
    position: relative;
    margin-bottom: 10vh;
    background-size: contain;
    background-position: center center;
}

